	module.exports = {"data":{"markdownRemark":{"html":"<!---\nThis is an autogenerated file!\n\nPlease do not edit this file directly, but instead make changes to the\nsigs.yaml file in the project root.\n\nTo understand how this file is generated, see https://git.k8s.io/community/generator/README.md\n-->\n<h1>Windows Special Interest Group</h1>\n<p>Focuses on supporting Windows Server Containers for Kubernetes.</p>\n<h2>Meetings</h2>\n<ul>\n<li>\n<p>Regular SIG Meeting: <a href=\"https://zoom.us/my/sigwindows\">Tuesdays at 12:30 Eastern Standard Time (EST)</a> (weekly). <a href=\"http://www.thetimezoneconverter.com/?t=12:30&#x26;tz=Eastern%20Standard%20Time%20%28EST%29\">Convert to your timezone</a>.</p>\n<ul>\n<li><a href=\"https://docs.google.com/document/d/1Tjxzjjuy4SQsFSUVXZbvqVb64hjNAG5CQX8bK7Yda9w/edit#heading=h.kbz22d1yc431\">Meeting notes and Agenda</a>.</li>\n<li><a href=\"https://www.youtube.com/watch?v=7zawb3KT9Xk&#x26;list=PL69nYSiGNLP2OH9InCcNkWNu2bl-gmIU4\">Meeting recordings</a>.</li>\n</ul>\n</li>\n</ul>\n<h2>Leadership</h2>\n<h3>Chairs</h3>\n<p>The Chairs of the SIG run operations and processes governing the SIG.</p>\n<ul>\n<li>Michael Michael (<strong><a href=\"https://github.com/michmike\">@michmike</a></strong>), Apprenda</li>\n<li>Patrick Lang (<strong><a href=\"https://github.com/patricklang\">@patricklang</a></strong>), Microsoft</li>\n</ul>\n<h2>Contact</h2>\n<ul>\n<li><a href=\"https://kubernetes.slack.com/messages/sig-windows\">Slack</a></li>\n<li><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-windows\">Mailing list</a></li>\n<li><a href=\"https://github.com/kubernetes/community/labels/sig%2Fwindows\">Open Community Issues/PRs</a></li>\n</ul>\n<h2>GitHub Teams</h2>\n<p>The below teams can be mentioned on issues and PRs in order to get attention from the right people.\nNote that the links to display team membership will only work if you are a member of the org.</p>\n<p>The google groups contain the archive of Github team notifications.\nMentioning a team on Github will CC its group.\nMonitor these for Github activity if you are not a member of the team.</p>\n<table>\n<thead>\n<tr>\n<th>Team Name</th>\n<th align=\"center\">Details</th>\n<th align=\"center\">Google Groups</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>@kubernetes/sig-windows-bugs</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-windows-bugs\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-windows-bugs\">link</a></td>\n<td>Bug Triage and Troubleshooting</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-windows-feature-requests</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-windows-feature-requests\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-windows-feature-requests\">link</a></td>\n<td>Feature Requests</td>\n</tr>\n<tr>\n<td>@kubernetes/sig-windows-misc</td>\n<td align=\"center\"><a href=\"https://github.com/orgs/kubernetes/teams/sig-windows-misc\">link</a></td>\n<td align=\"center\"><a href=\"https://groups.google.com/forum/#!forum/kubernetes-sig-windows-misc\">link</a></td>\n<td>General Discussion</td>\n</tr>\n</tbody>\n</table>\n<!-- BEGIN CUSTOM CONTENT -->\n<h2>Getting Started</h2>\n<p>If you're looking for information on building and running containers on Windows, you can get started at <a href=\"http://aka.ms/windowscontainers\">http://aka.ms/windowscontainers</a> .</p>\n<p>Kubernetes 1.9 includes beta support for Windows Server containers and has an up-to-date <a href=\"https://kubernetes.io/docs/getting-started-guides/windows/\">Getting Started Guide for Windows</a> .</p>\n<p>Some additional guides are available, but may refer to older releases:</p>\n<ul>\n<li>Using host gateway networking: <a href=\"https://docs.microsoft.com/en-us/virtualization/windowscontainers/kubernetes/getting-started-kubernetes-windows\">https://docs.microsoft.com/en-us/virtualization/windowscontainers/kubernetes/getting-started-kubernetes-windows</a> </li>\n<li>Using OVN as demonstrated at Google Cloud Next '17: <a href=\"https://github.com/apprenda/kubernetes-ovn-heterogeneous-cluster\">https://github.com/apprenda/kubernetes-ovn-heterogeneous-cluster</a></li>\n</ul>\n<h2>Contributing</h2>\n<p>We're currently using a public <a href=\"https://trello.com/b/rjTqrwjl/windows-k8s-roadmap\">Trello board</a> in the weekly SIG-Windows meeting to track items with active progress for each release. That's a great place to start to get the most up-to-date status, then feel free to join us on the Slack channel anytime with questions.</p>\n<p>As always, you can also find the active list of open issues and proposals <a href=\"https://github.com/kubernetes/kubernetes/labels/sig%2Fwindows\">Open Code Issues and PRs for SIG/Windows</a> on GitHub.</p>\n<h2>API tracking</h2>\n<p>We also have an <a href=\"https://github.com/kubernetes/community/blob/master/sig-windows/api-tracking.md\">API tracking</a> document that we're building out to track areas where the APIs may not be implemented or need to change for Windows.</p>\n<h2>Meeting Recordings</h2>\n<p>Past meetings are in the <a href=\"https://www.youtube.com/playlist?list=PL69nYSiGNLP2OH9InCcNkWNu2bl-gmIU4&#x26;jct=LZ9EIvD4DGrhr2h4r0ItaBmco7gTgw\">Recorded Meetings Playlist on Youtube</a></p>\n<!-- END CUSTOM CONTENT -->"},"site":{"siteMetadata":{"sigs":["sig-api-machinery","sig-apps","sig-architecture","sig-auth","sig-autoscaling","sig-aws","sig-azure","sig-big-data","sig-cli","sig-cloud-provider","sig-cluster-lifecycle","sig-cluster-ops","sig-contributor-experience","sig-docs","sig-gcp","sig-ibmcloud","sig-instrumentation","sig-multicluster","sig-network","sig-node","sig-openstack","sig-product-management","sig-release","sig-scalability","sig-scheduling","sig-service-catalog","sig-storage","sig-testing","sig-ui","sig-vmware","sig-windows"]}}},"pathContext":{"slug":"/sig-windows/"}}

